@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@font-face {
  font-family: 'EmojiFont1'; /*a name to be used later*/
  /* src: url('http://domain.com/fonts/font.ttf'); URL to font */
  src: url(/static/media/NotoEmoji-Regular.77705799.ttf);
}

@font-face {
  font-family: 'EmojiFont2'; /*a name to be used later*/
  /* src: url('http://domain.com/fonts/font.ttf'); URL to font */
  src: url(/static/media/Symbola-AjYx.bd95c919.ttf);
}

:root {
  --bgColor: #ffffff;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  background-color: var(--bgColor);
}

h1 h2 h3 h4 {
  font-weight: bold;
}

h3 {
  font-weight: bolder;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Roboto, monospace;
}

.emoji {
  font-family: 'EmojiFont1', 'EmojiFont2';
  color: white;
  font-size: 1.5rem;
}

.center {
  text-align: center;
}

.navbar {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.navbar-link {
  display: flex;
  align-items: center;
  font-size: 1rem;
}

.btn-brown {
  background-color: #ffffff;
  background-color: var(--bgColor);
}

.workshop_title {
  color: #ba947e;
}

.card_label {
  color: white;
}

.center {
  text-align: center;
}
.row-full {
  width: 100vw;
  position: relative;
  margin: 0;
  justify-content: center;
  max-width: -webkit-fill-available;
  max-width: -moz-available;
  max-width: fill-available;
  max-height: 100vh;
  /* height: 100px;
  margin-top: 100px; */
  /* left: 50%; */
}

.graph-col {
  padding: 10px;
}
.graph-card {
  margin-bottom: 20px;
  background: white;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
h4,
h5 {
  text-align: center;
  margin: 20px;
}
.legend-sector {
  font-size: 10px;
}

